.Blogs{
    padding-left: 10vw;
  padding-right: 10vw;
  padding-top: 3vh;
  text-align: left;
}
.blogL{
    color: slateblue;
}

.blog{
  width: 13cm;

  padding: 0.5vh 1vw 0vh 1vw;
  /* border: 0.7px solid gray; */
  border-radius: 7px;
  
  
  box-shadow: -1px -1px 5px rgba(255,255,255,0.45), 5px 5px 9px rgba(94,104,121,0.3);
  cursor: pointer;
}

.blog:hover{

  transform: scale(1.10);
  transition: .10s;
  
}
.blogSection{
  display: flex;
  flex-wrap: wrap;
  column-gap: 3vw;
  row-gap: 5vh;
  /* padding-left: 5vw; */
  justify-content: center;

}


@media screen and (max-width : 1470px){
  .blog{
    width: 12cm;
  }
}
@media screen and (max-width : 1360px){
  .blog{
    width: 11cm;
  }
}
@media screen and (max-width : 1250px){
  .blog{
    width: 10cm;
  }
}
@media screen and (max-width : 1140px){
  .blog{
    width: 9cm;
  }
}
@media screen and (max-width : 1030px){
  .blog{
    width: 8cm;
  }
  #favButtonInd{
    width: 2cm;
  }
}

@media screen and (max-width : 910px){
  .blog{
    width:7cm;
  }
}
@media screen and (max-width:800px){
  .blog{
      width: 80vw;
  }
}


