.emailMe{
    width: 4cm;
    height: 4cm;
}
.openMail{
    display: flex;
    align-items: center;
    column-gap: .5cm;
    padding-left: 5vw;
}

.mailToButton{
    border: none;
    border-radius: 5px;
    background-color: #a0c1e1;
    height: 1cm;
    font-size: 20px;
    cursor: pointer;
    transition: 1s all ease;
    // padding-top: 5vh;
    font-family: Ubuntu;
}
.mail{
    display: flex;
    flex-direction: column;
    row-gap: .3cm;
    align-items: center;
}
.connectWithMe{
    padding-left: 5vw;
}
.mailToButton:hover{
    background-color: blueviolet;
    color: whitesmoke;
}
@media screen and (max-width : 600px){
    .openMail{
        flex-direction: column;
        align-content: center;
    }
    .emailMe{
        width: 3.5cm;
        height: 3.5cm;
    }
}

@media screen and (max-width : 400px){
    .openMail{
        flex-direction: column;
        align-content: center;
    }
    .emailMe{
        width: 3cm;
        height: 3cm;
    }
    .mailToButton{
        height: .8cm;
        font-size: 18px;
    }
}