.headImgs{
    display: flex;
    align-items: center;
}
.headImg{
    width: 1.3cm;
    height: 1.3cm;
    
}
.tagLine{
    overflow: hidden;
    white-space: nowrap;
    animation: typing 3s steps(40, end);
}

@keyframes typing {
    from { width: 0 }
    to { width: 100% }
  }


.skillSet{
    display: flex;
    column-gap: .5cm;
    row-gap: .5cm;
    flex-wrap: wrap;
    padding-top: 5vh;
}

.skillImg{
    width: 2cm;
    height: 2cm;
}

@media screen and (max-width:400px){
    .skillImg{
        width: 1.5cm;
        height: 1.5cm;
    }
}